body {
  margin: 0;
}

:root {
  --color-hover: #00acdc;
  --color-default: #000;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
  line-height: 60px;
}

.nav {
  text-transform: uppercase;
  display: inline-block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__link {
  height: 60px;
  padding: auto 0;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link.is-active {
  height: 60px;
  line-height: 60px;
  text-decoration: none;
}

.nav__link.is-active:link {
  color: var(--color-hover);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--color-hover);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link.is-active:active {
  color: var(--color-hover);
}

.nav__link:link, .nav__link:visited {
  color: var(--color-default);
}

.nav__link:hover, .nav__link:active {
  color: var(--color-hover);
}

.nav__link:active:after {
  content: "";
  background-color: var(--color-hover);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
